.cbxwpbookmars-post-list-icon {
  position: relative;
}

.cbxwpbookmars-post-list-icon:before {
  content: " ";
  background-image: url("../img/widget_icons/icon_post_list.png");
  background-size: contain;
  width: 28px;
  height: 28px;
  display: block;
  overflow: hidden;
  background-size: contain;
  background-repeat: no-repeat;
  margin: 0 auto;
}

